@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');

h1{
    color: aliceblue;
}

.main-nav{
    background: #131313;
    padding: 0.7vw;
    width: 40vw;
    border-radius: 0 0 0 1.5vw;
    display: flex;
    justify-content: space-evenly;
    box-shadow: rgb(0, 0, 0) 0vw 0vw 0.7vw 0.1vw;
    
}

.logo p{
    color: whitesmoke;
    font-family: 'Poppins', sans-serif;
    font-size: 2.3vw;

}

.logo{
    background: #000;
    border-bottom-right-radius: 50%;
    border-top-right-radius: 10%;
    padding: 0.5svw 2.2vw;
}

.navContainer{
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: fixed;
}

.nav-item-wrapper-div{
    background: #000;
    border-radius:1.5vw ;
    align-items: center;
    display: flex;
    justify-content: center;
    width: 20%;
    transition: ease 15ms;
  
}



.nav-item-wrapper{
    background: #000000;
    padding: 0.6vw;
    border-radius:1.5vw ;
    align-items: center;
    display: flex;
    justify-content: center;
    width: 20%;
    transition: ease 15ms;
}
.nav-item-wrapper:hover {
    background: #000;
    padding: 0.6vw;
    border-radius:1.5vw ;
    align-items: center;
    display: flex;
    justify-content: center;
    width: 20%;
    scale: 1.01;
    transition: ease 15ms;
    box-shadow: rgb(22, 22, 22) 0vw 0vw 0.3vw 0.3vw;
   
}
.nav-item-wrapper:hover > .nav-item-wrapper-div{

    animation: shake 0.3s;
    animation-iteration-count:1
}


@keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    10% { transform: translate(-1px, -2px) rotate(-1deg); }
    20% { transform: translate(-3px, 0px) rotate(1deg); }
    30% { transform: translate(3px, 2px) rotate(0deg); }
    40% { transform: translate(1px, -1px) rotate(1deg); }
    50% { transform: translate(-1px, 2px) rotate(-1deg); }
    60% { transform: translate(-3px, 1px) rotate(0deg); }
    70% { transform: translate(3px, 1px) rotate(-1deg); }
    80% { transform: translate(-1px, -1px) rotate(1deg); }
    90% { transform: translate(1px, 2px) rotate(0deg); }
    100% { transform: translate(1px, -2px) rotate(-1deg); }
  }
